import React from 'react';
import { Link, useRouteMatch } from 'react-router-dom';

function Home() {

  const { url } = useRouteMatch();

  return (
    <div className="home-container">
      <h1 className="home-container__title">Приложение для просмотра постов, альбомов и информации о пользователях</h1>
      <Link className="button home-container__button" to={`${url}users`}>Пользователи</Link>
      <Link className="button home-container__button" to={`${url}posts`}>Посты</Link>
      <Link className="button home-container__button" to={`${url}albums`}>Альбомы</Link>
    </div>
  );
}

export default Home;