import React from 'react';
import { NavLink, useRouteMatch } from 'react-router-dom';

function NavBar() {

  const { url } = useRouteMatch();

  return (
    <nav className="menu">
      <ul className="menu__list">
        <li className="menu__list-item">
          <NavLink exact className="menu__item" activeClassName="menu__item_active" to={`${url}users`}>Пользователи</NavLink>
        </li>
        <li className="menu__list-item">
          <NavLink className="menu__item" activeClassName="menu__item_active" to={`${url}posts`}>Посты</NavLink>
        </li>
        <li className="menu__list-item">
          <NavLink className="menu__item" activeClassName="menu__item_active" to={`${url}albums`}>Альбомы</NavLink>
        </li>
      </ul>

    </nav>
  );
}

export default NavBar;